import { bearSvg, blink, closeEyes, openEyes, setProgress } from "./bear";
import { loadModels, startVideo } from "./faceDetection";
var $container = document.querySelector(".login h1 a");
var $pwInput = document.querySelector("input#user_pass");
$container.innerHTML = bearSvg;
var progressIndicator = document.createElement("span");
progressIndicator.classList.add("shfi-progress");
document.querySelector("#loginform").appendChild(progressIndicator);
/**
 * Eye functions
 */
window.setInterval(function () { return blink(); }, 5000);
$pwInput.addEventListener("focusin", function () { return closeEyes(); });
$pwInput.addEventListener("focusout", function () { return openEyes(); });
/**
 * Video
 */
var smiling = null;
var progress = 0;
var submit = document.querySelector("#wp-submit");
var updateProgress = function (newProgress) {
    if (newProgress <= 0) {
        newProgress = 0;
    }
    else if (newProgress >= 100) {
        newProgress = 100;
        submit.removeAttribute("disabled");
        clearInterval(smilingInterval);
    }
    progress = newProgress;
    setProgress(newProgress);
};
loadModels().then(function () {
    return startVideo(function (value) {
        smiling === null && submit.setAttribute("disabled", "true");
        smiling = value >= 0.9;
    });
});
var smilingInterval = window.setInterval(function () {
    smiling ? updateProgress(progress + 1) : updateProgress(progress - 1);
}, 20);
